<template>
	<div class="pwd-manage-transaction-apply">
		<div class="head-hint">请输入信用卡卡号</div>
		<div class="field-part">
			<mt-field v-model="card" label="信用卡卡号" placeholder="仅限安徽农金系统内的信用卡"></mt-field>
		</div>
		<div class="tip-hint">
			该信用卡已申请过交易密码，不可重复申请
		</div>
		<div class="button-bottom">
			<mt-button @click.native="next" type="primary" size="large" :disabled="!card">下一步</mt-button>
		</div>
	</div>
</template>

<script>
	
export default {
	data() {
		return {
			card: ''
		};
	},
	methods: {
		next() {
			this.$router.push({name: 'CreditManagePwdTransactionApplySet', params: {card: this.card}});
		}
	}
};

</script>


