<template>
	<div class="more-service-one-active-detail">
		<mt-cell title="信用卡卡号：" :value="detail.card"></mt-cell>
		<mt-field v-model="detail.cvn2" label="CNV2：" placeholder="卡片背面签名栏后3位数字"></mt-field>
		<mt-field v-model="detail.date" label="卡片有效期：" placeholder="卡片正面带'/'的4位数字，如0619"></mt-field>
		<mt-field v-model="detail.idCard" label="身份证号码：" placeholder="请输入身份证号码"></mt-field>
		<div class="field-action">
            <mt-button @click.native="next()" type="primary">下一步</mt-button>
		</div>
	</div>
</template>

<script>

import service from '@/services/credit/more-service';

export default{
	data() {
		return {
			detail: {
				card: '',
				cvn2: '',
				date: '',
				idCard: ''
			}
		};
	},
	methods: {
        next() {
        	var _error = [];
            if( this.$validate.isEmpty(this.detail.cvn2) ) {
                _error.push( '请输入CVN2' );
            }
            if( this.$validate.isEmpty(this.detail.date) ) {
                _error.push( '请输入卡片有效期' );
            }
            if( this.$validate.isEmpty(this.detail.idCard) ) {
                _error.push( '请输入身份证号码' );
            }
            if( _error.length > 0 ) {
                this.$msg(_error[0]);
                return;
            }
        	this.checkCreditMessage();
        },
        checkCreditMessage() {
            service.checkCreditMessage()
            .then((data) => {
                this.$router.push({name: 'OneActivePassword'});
            }, (err) => {
                this.$msg('卡片激活信息有误，请认真核对并重新输入');
            });
        }
	},
	created() {
		this.detail = this.$ls.getItem('activeCard');
	},
	beforeRouteLeave (to, from, next) {
        if(to.meta['groupName'] === from.meta['groupName']){
            this.$ls.setItem('activeCard', this.detail);
        }else{
            this.$ls.removeItem('activeCard');
        }
        next( true );
    }
};

</script>

<style lang="scss">
.more-service-one-active-detail{
}
</style>


