<template>
	<div class="more-service-quick-loss">
        <mt-cell @click.native="showSelectorPaymentAccount()" title="信用卡卡号：" placeholder="" v-model="account" is-link>
        </mt-cell>
		<selector-payment-account ref="selectorPaymentAccount" :options="selectorPaymentAccount" ></selector-payment-account>
		<cell-password :options=" cellPassword "></cell-password>
		<mt-cell class="mark" title="申请卡片挂失可能会产生手续费">
			<a @click="toResetPwd" href="javascript:;" class="forget-pwd">忘记查询密码？</a>
		</mt-cell>
		<div class="field-action">
            <submit-button @submit="submit" :submitStatus="submitStatus" value="确认挂失"></submit-button>
		</div>
	</div>
</template>

<script>

import service from '@/services/credit/more-service';

export default{
	data() {
		return {
			account: '',
            submitStatus: false,
            cellPassword: {
                title: '查询密码：',
                rule: 'number' // number or mix
            },
			selectorPaymentAccount: {
				type: 'credit',
                checked: {},
                showWarn: false,
                accountList: [
                    {
                        id: 1,
                        name: '郭振宇',
                        account: '625228***0089',
                        type: '储蓄卡',
                        isDefault: true,
                        idCardType: '身份证',
                        idCard: '550505****50505'
                    },
                    {
                        id: 2,
                        name: '郭振宇2',
                        account: '625228***0025',
                        type: '储蓄卡'
                    },
                    {
                        id: 3,
                        name: '郭振宇3',
                        alias: '',
                        account: '625228***0109',
                        type: '储蓄卡',
                        bankName: '安徽农金'
                    }
                ]
            }
		};
	},
    watch: {
        account: function(val) {
            if(val){
                this.submitStatus = true;
            }else{
                this.submitStatus = false;
            }
        }
    },
	methods: {
		showSelectorPaymentAccount() {
            this.$refs.selectorPaymentAccount.show().then(data => {
                this.selectorPaymentAccount.checked = data.item;
                this.account = data.item.account;
            }, data => {
            });
        },
        submit() {
        	this.submitQuickLoss();
        },
        toResetPwd() {
            this.$router.push({name: 'CreditManagePwdSearchReset'});
        },
        submitQuickLoss() {
            service.submitQuickLoss()
            .then((data) => {
                this.$router.push({name: 'QuickLossSuccess'});
            }, (err) => {
                this.$msg('该卡已挂失');
            });
        }
	},
	created() {
		this.account = this.account || this.$store.state.keepSearch.account;
        this.account = this.$route.params.card;
	},
	beforeRouteLeave (to, from, next) {
        if(to.meta['groupName'] === from.meta['groupName']){
            this.$store.dispatch('updateKeepSearch', {key: 'account', value: this.account});
        }else{
            this.$store.dispatch('updateKeepSearch', {key: 'account', value: ''});
        }
        next( true );
    }
};

</script>

<style lang="scss">
.more-service-quick-loss{
	.mint-cell-title,{
		width: 3.6rem;
	}
    .mint-cell.mint-select{
        .mint-cell-title{
            width: 3.6rem;
        }
    }
    .mark .mint-cell-text{
    	font-size: 12px;
    }
	.forget-pwd{
		color: #26a2ff;
		font-size: 12px;
	}
}
</style>


