<template>
	<div class="auto-repay-not-open">
        <div class="cell-part">
    		<mt-cell title="自动还款">
    			<mt-switch v-model="isOpen"></mt-switch>
    		</mt-cell>
        </div>
		<div v-if="!isOpen" class="field-hint">
			开通自动还款功能后，将在信用卡账单还款日，自动从指定的还款账号中扣缴信用卡账款
		</div>
        <template  v-if="isOpen">
    		<div class="cell-part">
    			<mt-cell @click.native="showSelectorPaymentAccount()" title="自动还款账号：" placeholder="" v-model="account" is-link>
            	</mt-cell>
            	<selector-payment-account ref="selectorPaymentAccount" :options="selectorPaymentAccount" ></selector-payment-account>
            	<mt-cell class="mint-select" title="自动还款方式：" is-link>
    	            <select class="mint-field-core" v-model="type">
    	                <option value="">全款还款</option>
    	                <option value="1">部分还款</option>
    	            </select>
    	        </mt-cell>
            </div>
    	    <div class="cell-part">
                <cell-password :options=" cellPassword "></cell-password>
            </div>
            <div class="field-warn text-right">
            	<a @click="toResetPwd" href="javascript:;" class="forget-pwd">忘记查询密码？</a>
            </div>
            <div class="button-bottom">
            	<submit-button @click.native="submit" :submitStatus="submitStatus" type="primary" value="确认开通" size="large"></submit-button>
            </div>
        </template>
	</div>
</template>

<script>

import service from '@/services/credit/manage';
import * as commService from '@/services/bank/selector';

export default {
	data() {
		return {
			isOpen: false,
			account: '',
			type: '',
			submitStatus: false,
			selectorPaymentAccount: {
                checked: {},
                showAddBtn: false,
                showWarn: false,
                accountList: []
            },
            cellPassword: {
                title: '查询密码：',
                rule: 'number' // number or mix
            }
		};
	},
	watch: {
		account: function(val){
			if(val) this.submitStatus = true;
			else this.submitStatus = false;
		}
	},
    created() {
        commService.getAccountList().then((res) => {
            this.selectorPaymentAccount.accountList = res.data;
            let defaultAccount = res.data.filter(( item )=>{ return item.isDefault })[0] || {};
            this.selectorPaymentAccount.checked = defaultAccount;
            this.dataForm.account = defaultAccount.ACCOUNT;
        }, (err) => {
            console.log( err );
        });
    },
	methods: {
		showSelectorPaymentAccount() {
            this.$refs.selectorPaymentAccount.show().then(data => {
                this.selectorPaymentAccount.checked = data.item;
                this.account = data.item.ACCOUNT;
            }, data => {
            });
        },
        toResetPwd() {
            this.$router.push({name: 'CreditManagePwdSearchReset'});
        },
        submit() {
            service.setAutoRepay()
            .then((data) => {
                this.$router.push({name: 'CreditManageAutoRepayNotOpenSuccess'});
            }, (err) => {
                console.log(err);
            });
        }
	}
};

</script>

<style lang="scss">

.auto-repay-not-open{
	.mint-cell-title,{
		width: 3.6rem;
	}
	.mint-cell.mint-select{
        .mint-cell-title{
            width: 3.6rem;
        }
    }
    .forget-pwd{
		color: #308cf8;
		font-size: 13px;
	}
}

</style>


