<template>
	<div class="card-set-success">
		<div class="submit-contain">
			<div class="result-icon">
                <i class="mintui mintui-success"></i>
            </div>
			<div>{{msg}}</div>
			<div class="field-action">
				<mt-button @click.native="back" size="small" type="primary">返回信用卡管理</mt-button>
			</div>
		</div>
	</div>
</template>

<script>
	
export default {
	data() {
		return {};
	},
	computed: {
		msg: function(){
			let type = this.$route.params.type;
			if(type === 'rename'){
				return '别名修改成功';
			}else if(type === 'cost'){
				return '消费凭密修改成功';
			}else{
				return '卡片删除成功';
			}
		}
	},
	methods: {
		back() {
			this.$router.push({name: 'CreditManage'});
		}
	}
};

</script>

<style lang="scss">
.card-set-success{
	position: absolute;
	width: 100%;
	height:100%;
	top: 0;
	left: 0;
	background-color: #fff;
	z-index: 1;
	text-align: center;
	font-size: 14px;
	.submit-contain{
		margin-top: 50%;
	}

}
</style>


