<template>
	<div class="set-service-update">
		<div class="field-part">
			<mt-cell class="mint-select" title="账单寄送方式：" is-link>
	            <select class="mint-field-core" v-model="detail.sendType">
	                <option value="">电子邮件</option>
	                <option value="1">非电子邮件</option>
	            </select>
	        </mt-cell>
	        <mt-field v-model="detail.mail" label="电子邮箱：" placeholder="请输入电子邮箱"></mt-field>
		</div>
		<div class="field-part">
	        <mt-cell class="mint-select" title="邮寄地址：" is-link>
	            <select class="mint-field-core" v-model="detail.addressType">
	                <option value="">家庭地址</option>
	                <option value="1">单位地址</option>
	            </select>
	        </mt-cell>
	        <mt-field v-model="detail.address1" label="家庭地址：" placeholder="请输入家庭地址"></mt-field>
	        <mt-field v-model="detail.address2" label="单位地址：" placeholder="请输入单位地址"></mt-field>
		</div>
		<div class="field-part">
        	<cell-password :options=" cellPassword "></cell-password>
		</div>
        <div class="field-warn text-right">
        	<a @click="toResetPwd" href="javascript:;" class="forget-pwd">忘记查询密码？</a>
        </div>
        <div class="button-bottom">
			<mt-button @click.native="update" type="primary" size="large">保存</mt-button>
        </div>
	</div>
</template>

<script>

import service from '@/services/credit/manage';

export default {
	data() {
		return {
			detail: {
				sendType: '',
				mail: '',
				addressType: '',
				address1: '',
				address2: ''
			},
			cellPassword: {
                title: '查询密码：',
                rule: 'number' // number or mix
            }
		};
	},
	methods: {
		toResetPwd() {
            this.$router.push({name: 'CreditManagePwdSearchReset'});
        },
		update() {
			this.updateSetService();
		},
		updateSetService() {
			service.updateSetService()
			.then((data) => {
				this.$router.push({name: 'CreditManageSetServiceSuccess'});
			}, (err) => {
				console.log(err);
			});
		}
	}
};

</script>

<style lang="scss">
	
.set-service-update{
	.mint-cell-title,{
		width: 3.6rem;
	}
	.mint-cell.mint-select{
        .mint-cell-title{
            width: 3.6rem;
        }
    }
    .forget-pwd{
		color: #308cf8;
		font-size: 13px;
	}
}

</style>


