<template>
	<div class="credit-manage-card-set">
		<div class="cell-part">
			<mt-cell title="信用卡卡号" :value="card"></mt-cell>
		</div>
		<div class="cell-part">
			<mt-cell @click.native="toSet('CreditManageCardSetRename')" title="别名" :value="detail.name || '未设置'" is-link></mt-cell>
		</div>
		<div class="cell-part">
			<mt-cell @click.native="toSet('CreditManageCardSetCost')" title="消费凭密" :value="detail.msg" is-link></mt-cell>
		</div>
		<div class="cell-part">
			<mt-cell @click.native="toSet('QuickLoss')" title="快速挂失" is-link></mt-cell>
			<mt-cell @click.native="toSet('PatchCard')" title="闪电补卡" is-link></mt-cell>
		</div>
		<div class="delete-button">
			<a @click="submit" href="javascript:;">从手机银行删除此卡</a>
		</div>
	</div>
</template>

<script>

import { MessageBox } from 'mint-ui-rem-dpr';
import service from '@/services/credit/manage';

export default {
	data() {
		return {
			card: '',
			detail: {
				name: '',
				msg: ''
			}
		};
	},
	methods: {
		toSet(name) {
			this.$router.push({name: name, params: {card: this.card}});
		},
		submit() {
			var self = this;
			let card = self.card.substring((self.card.length - 4), self.card.length);
			MessageBox({
			 	title: '提示',
			  	message: `确定要删除尾号为${card}的信用卡吗？`,
			  	showCancelButton: true,
			  	confirmButtonText: '确认删除'
			}).then(action => {
				if(action === 'confirm'){
					self.deleteCredit();
				}
			});
		},
		deleteCredit() {
			service.deleteCredit()
			.then((data) => {
				this.$router.push({name: 'CreditManageCardSetSuccess', params: {type: 'delete'}});
			}, (err) => {
				console.log(err);
			});
		}

	},
	created() {
		this.card = this.$route.params.card;
	}
};

</script>

<style lang="scss">
	
.credit-manage-card-set{
	.delete-button{
		margin-top: 80px;
		padding: 24px 0;
		text-align: center;
		background-color: #fff;
		a{
			color: #FF511A;
			font-size: 15px;
		}
	}
}

</style>


