<template>
	<div class="credit-manage-card-set-rename">
		<mt-field v-model="rename" label="别名" placeholder="请输入别名，例如：理财卡"></mt-field>
		<div class="field-action">
			<submit-button @click.native="submit" :submitStatus="submitStatus" value="保存"></submit-button>
		</div>
	</div>
</template>

<script>

import service from '@/services/credit/manage';

export default {
	data() {
		return {
			rename: '',
			submitStatus: false
		};
	},
	watch: {
		rename: function(val){
			if(val) this.submitStatus = true;
			else this.submitStatus = false;
		}
	},
	methods: {
		submit() {
			this.setCreditAlias();
		},
		setCreditAlias() {
			service.setCreditAlias()
			.then((data) => {
				this.$router.push({name: 'CreditManageCardSetSuccess', params: {type: 'rename'}});
			}, (err) => {
				console.log(err);
			});
		}
	}
};

</script>


