<template>
	<div class="more-service-one-active-password">
		<mt-cell title="信用卡卡号：" :value="detail.card"></mt-cell>
		<mt-cell title="请设置查询密码："></mt-cell>
		<cell-password :options=" cellPassword1 "></cell-password>
		<cell-password :options=" cellPassword2 "></cell-password>
		<mt-cell title="请设置交易密码："></mt-cell>
		<cell-password :options=" cellPassword3 "></cell-password>
		<cell-password :options=" cellPassword4 "></cell-password>
		<div class="field-action">
            <mt-button @click.native="showDialogSmsVerify()" type="primary">下一步</mt-button>
            <dialog-sms-verify ref="dialogSmsVerify" :options=" dialogSmsVerify "></dialog-sms-verify>
		</div>
	</div>
</template>

<script>

import service from '@/services/credit/more-service';

export default{
	data() {
		return {
			detail: {
				card: '',
				cvn2: '',
				date: '',
				idCard: ''
			},
			cellPassword1: {
                title: '查询密码：',
                rule: 'number' // number or mix
            },
            cellPassword2: {
                title: '确认查询密码：',
                rule: 'number' // number or mix
            },
            cellPassword3: {
                title: '交易密码：',
                rule: 'number' // number or mix
            },
            cellPassword4: {
                title: '确认交易密码：',
                rule: 'number' // number or mix
            },
            dialogSmsVerify: {
                title: '输入验证码完成激活',
                btnConfirmText: '完成激活',
                btnGetText: '获取验证码'
            }
		};
	},
	methods: {
        showDialogSmsVerify() {
            this.$refs.dialogSmsVerify.show().then((data) => {
                this.activeCredit();
            }, (data) => {
            });
        },
        checkIsApplySearchPwd() {
            service.checkIsApplySearchPwd()
            .then((data) => {
            }, (err) => {
                console.log(err);
            });
        },
        activeCredit() {
            service.activeCredit()
            .then((data) => {
                this.$router.push({name: 'OneActiveSuccess'});
            }, (err) => {
                console.log(err);
            });
        }
	},
	created() {
		this.detail = this.$ls.getItem('activeCard');
        this.checkIsApplySearchPwd();
	}
};

</script>

<style lang="scss">
.more-service-one-active-password{
}
</style>


