<template>
	<div class="credit-manage-card-set-cost">
		<mt-cell title="信用卡卡号" :value="card"></mt-cell>
		<mt-cell class="mint-select" title="消费凭密" is-link>
            <select class="mint-field-core" v-model="costType">
                <option value="">签名+交易密码</option>
                <option value="1">仅签名</option>
            </select>
        </mt-cell>
		<cell-password :options=" cellPassword "></cell-password>
		<mt-cell>
                <a class="forget-pwd">忘记查询密码</a>
            </mt-cell>
		<div class="field-action">
			<mt-button @click.native="submit" type="primary">保存</mt-button>
		</div>
	</div>
</template>

<script>

import service from '@/services/credit/manage';

export default {
	data() {
		return {
			card: '',
			costType: '',
			cellPassword: {
                title: '查询密码：',
                rule: 'number' // number or mix
            }
		};
	},
	watch: {
		rename: function(val){
			if(val) this.submitStatus = true;
			else this.submitStatus = false;
		}
	},
	methods: {
		submit() {
			this.setCreditCost();
		},
		setCreditCost() {
			service.setCreditCost()
			.then((data) => {
				this.$router.push({name: 'CreditManageCardSetSuccess', params: {type: 'cost'}});
			}, (err) => {
				console.log(err);
			});
		}
	},
	created() {
		this.card = this.$route.params.card;
	}
};

</script>

<style lang="scss">
	
.credit-manage-card-set-cost{
	.forget-pwd{
		color: #26a2ff;
		font-size: 12px;
	}
}

</style>


