<template>
    <div class="bill">
        <div class="field-part">
            <cell class="account" title="信用卡卡号" @click.native="showCreditAccount()" v-model="formData.creditAccount" isLink select></cell>
            <selector-payment-account ref="creditAccount" :options=" creditAccount "></selector-payment-account>
            <div class="panel">
                <div class="panel-top">
                    <p>剩余未还（元）</p>
                    <p class="amount">12,390.97</p>
                    <p>最终还款日：6月27日</p>
                </div>
                <div class="panel-bottom">
                    <p class="now">本期应还：12,390.97元</p>
                    <p class="low">最低还款：2，145.00元</p>
                </div>
            </div>
        </div>
        <div class="field-part">
            <cell class="account" title="借记卡卡号" @click.native="showDebitAccount()" v-model="formData.debitAccount" isLink select></cell>
            <selector-payment-account ref="debitAccount" :options=" debitAccount "></selector-payment-account>
            <mt-cell class="field-self-style" title="还款金额">
                <input @focus="amountFocus()" @blur="amountBlur()" v-model="formData.amount" class="mint-field-core" type="number" placeholder="请输入还款金额">
            </mt-cell>
            <template v-if="showAmount">
                <mt-field label="全额还款"></mt-field>
                <mt-field label="最低还款"></mt-field>
            </template>
        </div>

        <div class="field-action">
            <mt-button type="default" size="large" @click="next">下一步</mt-button>
        </div>
        <div class="warn">
            手头有点紧?可使用惠分单分期哦
        </div>
    </div>
</template>

<script>
    import * as services from '@/services/credit/credit';
    import * as service from '@/services/bank/selector';
    export default {
        name: 'bill',
        data() {
            return {
                formData: {
                    creditAccount: '',
                    debitAccount: '',
                    amount: ''
                },
                creditAccount: {
                    title: '选择信用卡',
                    type: 'credit',
                    checked: {},
                    showAddBtn: true,
                    showWarn: false,
                    accountList: []
                },
                debitAccount: {
                    title: '选择借记卡',
                    type: 'payment',
                    checked: {},
                    showAddBtn: true,
                    showWarn: false,
                    accountList: []
                },
                showAmount: false
            };
        },
        created() {
            service.getAccountList()
                .then((res) => {
                    this.debitAccount.accountList = res.data;
                }, (err) => {
                    console.log(err);
                });
            services.queryCreditCard()
                .then((res) => {
                    this.creditAccount.accountList = res.data;
                }, (err) => {
                    console.log(err);
                });
        },
        methods: {
            showCreditAccount() {
                this.$refs.creditAccount.show().then((data) => {
                    this.creditAccount.checked = data.item;
                    this.formData.creditAccount = this.creditAccount.checked.account;
                }, (data) => {
                });
            },
            showDebitAccount() {
                this.$refs.debitAccount.show().then((data) => {
                    this.debitAccount.checked = data.item;
                    this.formData.debitAccount = this.debitAccount.checked.account;
                }, (data) => {
                });
            },
            amountFocus() {
                this.showAmount = true;
            },
            amountBlur() {
                this.showAmount = false;
            },
            next() {
                let _data = Object.assign({}, this.formData);
                if (this.$validate.isEmpty(_data.creditAccount)) {
                    this.$msg('请选择信用卡号');
                    return false;
                }
                if (this.$validate.isEmpty(_data.debitAccount)) {
                    this.$msg('请选择借记卡号');
                    return false;
                }
                if (this.$validate.isEmpty(_data.amount)) {
                    this.$msg('请输入还款金额');
                    return false;
                }
                if (!this.$validate.isAmount(_data.amount)) {
                    this.$msg('金额格式错误');
                    return false;
                }
                this.$router.push({ name: 'CreditBillCurrent' });
            }
        }
    };

</script>
<style lang="scss">
    .field-self-style {
        .mint-cell-title {
            width: 210px;
            flex: none;
        }

        .mint-cell-value {
            flex: 1;
            color: inherit;
            display: flex;
        }

        .mint-field-core {
            appearance: none;
            border-radius: 0;
            border: 0;
            flex: 1;
            outline: 0;
            line-height: 1.6;
            font-size: inherit;
            width: 100%;
        }
    }
</style>
<style lang="scss" scoped>
    .panel {
        text-align: center;
        padding: 50px 20px;
        margin-bottom: 30px;
        background-color: #e0e0e0;

        .panel-top {
            position: relative;
            padding-bottom: 15px;
            @include mini-border(bottom, #a9acb1);

            p+p {
                margin-top: 20px;
            }

            .amount {
                color: #ea7f31;
                font-size: 18px;
            }
        }

        .panel-bottom {
            display: flex;
            display: -webkit-box;
            display: -ms-flexbox;
            padding-top: 15px;

            p {
                flex: 1;
            }

            .now {
                text-align: left;
            }

            .low {
                text-align: right;
            }
        }
    }

    .account {
        background-color: #fff!important;
    }

    .warn {
        text-align: center;
        color: #26a2ff;
    }
</style>


