<template>
	<div class="pwd-manage-transaction-success">
		<tip-page :tipTitle="msg" tipButton="返回信用卡管理" @submit="back">
		</tip-page>
	</div>
</template>

<script>
	
export default {
	data() {
		return {};
	},
	computed: {
		msg: function(){
			let type = this.$route.params.type;
			if(type === 'apply'){
				return '交易密码申请成功';
			}else if(type === 'update'){
				return '交易密码修改成功';
			}else{
				return '交易密码重置成功';
			}
		}
	},
	methods: {
		back() {
			this.$router.push({name: 'CreditManage'});
		}
	}
};

</script>

<style lang="scss">
.pwd-manage-transaction-success{
}
</style>


