<template>
    <div class="bill">
        <div class="field-part account-select">
            <cell title="信用卡卡号" @click.native="showAccount()" v-model="formData.account" isLink select></cell>
            <selector-payment-account ref="selectorPaymentAccount" :options=" selectorPaymentAccount "></selector-payment-account>
        </div>

        <div class="field-top">
            <div class="left">
                账单日：6月2日
            </div>
            <div class="right">
                还款日：6月27日
            </div>
        </div>

        <div class="amount-panel">
            <div>本期应还（元）</div>
            <div>
                <P class="amount">{{12390 | formatNumber(2)}}</P>
            </div>
            <div class="amount-class">
                <div class="amount-rest">
                    <p>剩余未还</p>
                    <p>{{12390 | formatNumber(2)}}</p>
                </div>
                <div class="amount-low">
                    <p>最低还款</p>
                    <p>{{2145 | formatNumber(2)}}</p>
                </div>
            </div>
            <div class="bill-btn">
                <mt-button class="btn" size="small">我要还款</mt-button>
                <mt-button class="btn" size="small">惠分单</mt-button>
            </div>
        </div>

        <div class="field-cell" @click="currentBill">
            本期账单明细 >
        </div>

        <div class="field-part">
            <mt-cell title="未出账单" :to="{name: 'CreditBillOut'}" is-link></mt-cell>
        </div>
        <div class="field-part">
            <mt-cell title="历史账单" :to="{name: 'CreditBillHistory'}" is-link></mt-cell>
        </div>
    </div>
</template>

<script>
    import * as service from '@/services/credit/credit';
    export default {
        name: 'bill',
        data() {
            return {
                formData: {
                    account: ''
                },
                selectorPaymentAccount: {
                    title: '选择付款账号',
                    type: 'credit',
                    checked: {},
                    showAddBtn: true,
                    showWarn: false,
                    accountList: []
                }
            };
        },
        created() {
            service.queryCreditCard()
                .then((res) => {
                    this.selectorPaymentAccount.accountList = res.data;
                }, (err) => {
                    console.log(err);
                });
        },
        methods: {
            showAccount() {
                this.$refs.selectorPaymentAccount.show().then((data) => {
                    this.selectorPaymentAccount.checked = data.item;
                    this.formData.account = this.selectorPaymentAccount.checked.account;
                }, (data) => {
                });
            },
            currentBill() {
                this.$router.push({ name: 'CreditBillCurrent' });
            }
        }
    };

</script>

<style lang="scss" scoped>
    .account-select {
        background-color: #fff;
    }

    .field-top {
        position: relative;
        display: flex;
        display: -webkit-box;
        display: -ms-flexbox;
        padding: 20px;
        background-color: #fafafa;
        @include mini-border(top);

        .left {
            flex: 1;
        }
    }

    .amount-panel {
        text-align: center;
        padding: 50px 0;
        margin-bottom: 15px;
        background-color: #e0e0e0;

        div+div {
            margin-top: 20px;
        }

        .amount {
            color: #ea7f31;
            font-size: 18px;
        }

        .amount-class {
            display: inline-block;

            .amount-rest {
                display: inline-block;
                padding-right: 15px;
            }

            .amount-low {
                display: inline-block;
                padding-left: 15px;
                border-left: 1px solid #999;
            }
        }

        .bill-btn {
            .btn {
                width: 220px;
            }
            .btn+.btn {
                margin-left: 20px;
            }
        }
    }

    .field-cell {
        position: relative;
        padding: 20px;
        margin-bottom: 15px;
        color: #26a2ff;
        background-color: #fff;
        text-align: center;
        @include mini-border(top, #999, after);
        @include mini-border(bottom, #999, before);
    }
</style>


