<template>
	<div class="more-service-apply-process">
		<mt-cell title="请输入申请信用卡时预留的身份证号"></mt-cell>
		<mt-field v-model="idCard" label="身份证：" type="text" placeholder="请输入身份证号码"></mt-field>
		<div class="field-action">
            <mt-button @click.native="showDialogSmsVerify()" type="primary">下一步</mt-button>
            <dialog-sms-verify ref="dialogSmsVerify" :options=" dialogSmsVerify "></dialog-sms-verify>
		</div>
	</div>
</template>

<script>

import { MessageBox } from 'mint-ui-rem-dpr';
import service from '@/services/credit/more-service';

export default{
	data() {
		return {
			idCard: '',
            dialogSmsVerify: {
                title: '输入验证码进行查询',
                btnConfirmText: '查看进度',
                btnGetText: '获取验证码'
            }
		};
	},
	methods: {
        back() {
        	history.back();
        },
        processSearch() {
            service.processSearch()
            .then((data) => {
                this.$router.push({name: 'ApplyProcessDetail', params: {detail: data.data.data}});
            }, (err) => {
                console.log(err);
            });
        },
        showDialogSmsVerify() {
            if(this.$validate.isEmpty(this.idCard)){
                this.$msg('身份证号码不能为空');
                return;
            }
            this.$refs.dialogSmsVerify.show().then((data) => {
                this.processSearch();
            }, (data) => {
            });
        }
	},
	created() {
	}
};

</script>

<style lang="scss">
.more-service-apply-process{
}
</style>


