<template>
	<div class="pwd-manage-serach-apply-set">
        <div class="field-part">
            <mt-cell title="信用卡卡号" :value="card"></mt-cell>  
        </div>
        <div class="field-part">
            <mt-field v-model="card1" label="CVN2" placeholder="卡片背面签名栏后3位数字"></mt-field>
        </div>
        <div class="tip-hint">
            账户信息有误，请重新输入
        </div>
        <div class="field-part">
    		<div class="head-hint">请设置查询密码</div>
    		<cell-password :options="cellPassword1"></cell-password>
    		<cell-password :options="cellPassword2"></cell-password>
        </div>
        <div class="tip-hint">
            两次输入的密码不一致
        </div>
		<div class="button-bottom">
			<mt-button @click.native="submit" type="primary" size="large">提交申请</mt-button>
			<dialog-sms-verify ref="dialogSmsVerify" :options=" dialogSmsVerify "></dialog-sms-verify>
		</div>
	</div>
</template>

<script>

import service from '@/services/credit/manage';

export default {
	data() {
		return {
			card: '',
			card1: '',
			cellPassword1: {
                title: '查询密码',
                rule: 'number' // number or mix
            },
            cellPassword2: {
                title: '确认查询密码',
                rule: 'number' // number or mix
            },
            dialogSmsVerify: {
                title: '输入验证码完成申请',
                btnConfirmText: '完成申请',
                btnGetText: '获取验证码'
            }
		};
	},
	methods: {
		submit() {
            this.$refs.dialogSmsVerify.show().then((data) => {
                this.applyTransactionPwd();
            }, (data) => {
            });
        },
        applyTransactionPwd() {
        	service.applyTransactionPwd()
        	.then((data) => {
        		this.$router.push({name: 'CreditManagePwdTransactionSuccess', params: {type: 'apply'}});
        	}, (err) => {
        		console.log(err);
        	});
        }
	},
	created() {
		this.card = this.$route.params.card;
	}
};

</script>


