<template>
	<div class="auto-repay-open">
        <div :class="{'cell-part': !isOpen}">
            <mt-cell class="auto-switch" title="自动还款">
                <mt-switch v-model="isOpen"></mt-switch>
            </mt-cell>
        </div>
		<template v-if="isOpen">
            <div class="cell-part">
    			<mt-cell title="原自动还款账号：" :value="account"></mt-cell>
    			<mt-cell title="原自动还款方式：" :value="type"></mt-cell>
            </div>
			
            <div class="cell-part">
                <div class="head-hint">
                    您可重新调整自动还款设置
                </div>
    			<mt-cell @click.native="showSelectorPaymentAccount()" title="新自动还款账号：" placeholder="" v-model="detail.account" is-link>
            	</mt-cell>
            	<selector-payment-account ref="selectorPaymentAccount" :options="selectorPaymentAccount" ></selector-payment-account>
            	<mt-cell class="mint-select" title="新自动还款方式：" is-link>
    	            <select class="mint-field-core" v-model="detail.type">
    	            	<option value="">请选择</option>
    	                <option value="1">全款还款</option>
    	                <option value="2">部分还款</option>
    	            </select>
    	        </mt-cell>
            </div>
            <template v-if="detail.account && detail.account !== '请选择'">
    	        <div class="cell-part">
    		        <cell-password :options=" cellPassword "></cell-password>
                </div>
	            <div class="cell-hint text-right">
	            	<a @click="toResetPwd" href="javascript:;" class="forget-pwd">忘记查询密码？</a>
	            </div>
	            <div class="button-bottom">
	            	<submit-button @click.native="submit" :submitStatus="submitStatus" type="primary" value="确认修改" size="large"></submit-button>
	            </div>
            </template>
		</template>
		<template v-if="!isOpen">
            <div class="cell-part">
			 <cell-password :options=" cellPassword "></cell-password>
            </div>
            <div class="cell-hint text-right">
            	<a @click="toResetPwd" href="javascript:;" class="forget-pwd">忘记查询密码？</a>
            </div>
            <div class="button-bottom">
            	<mt-button @click.native="closeSubmit" type="primary" size="large">确认关闭</mt-button>
            </div>
		</template>
	</div>
</template>

<script>

import service from '@/services/credit/manage';
import * as commService from '@/services/bank/selector';

export default {
	data() {
		return {
			isOpen: true,
			account: '',
			type: '',
			detail: {
				account: '请选择',
				type: ''
			},
			submitStatus: false,
			selectorPaymentAccount: {
                title: '请选择新还款账号',
                checked: {},
                showAddBtn: true,
                showWarn: false,
                accountList: []
            },
            cellPassword: {
                title: '查询密码：',
                rule: 'number' // number or mix
            }
		};
	},
	watch: {
		detail: {
			handler: function(item){
				if(item.account && item.account !== '请选择' && item.type){
					this.submitStatus = true;
				}else{
					this.submitStatus = false;
				}
			},
			deep: true
		}
	},
	methods: {
		showSelectorPaymentAccount() {
            this.$refs.selectorPaymentAccount.show().then(data => {
                this.selectorPaymentAccount.checked = data.item;
                this.detail.account = data.item.ACCOUNT;
            }, data => {
            });
        },
        toResetPwd() {
            this.$router.push({name: 'CreditManagePwdSearchReset'});
        },
        submit() {
        	service.setAutoRepay()
        	.then((data) => {
        		this.$router.push({name: 'CreditManageAutoRepayOpenSuccess'});
        	}, (err) => {
        		console.log(err);
        	});
        },
        closeSubmit() {
        	service.setAutoRepay()
        	.then((data) => {
        		this.$router.push({name: 'CreditManageAutoRepayCloseSuccess'});
        	}, (err) => {
        		console.log(err);
        	});
        },
        getAutoRepayDetail() {
        	service.getAutoRepayDetail()
        	.then((data) => {
        	}, (err) => {
        		console.log(err);
        	});
        }
	},
	created() {
        commService.getAccountList().then((res) => {
            this.selectorPaymentAccount.accountList = res.data;
            let defaultAccount = res.data.filter(( item )=>{ return item.isDefault })[0] || {};
            this.selectorPaymentAccount.checked = defaultAccount;
            this.account = defaultAccount.ACCOUNT;
        }, (err) => {
            console.log( err );
        });
		this.getAutoRepayDetail();
	}
};

</script>

<style lang="scss">

.auto-repay-open{
    .auto-switch:after{
        left: 0;
    }
    .auto-switch:last-child{
        background-image: none;
    }
	.mint-cell-title,{
		width: 3.6rem;
	}
	.mint-cell.mint-select{
        .mint-cell-title{
            width: 3.6rem;
        }
    }
    .forget-pwd{
		color: #308cf8;
		font-size: 13px;
	}
}

</style>


