<template>
	<div class="more-service-one-active">
		<mt-cell title="请输入信用卡卡号"></mt-cell>
		<mt-field v-model="detail.card" label="信用卡卡号：" type="text"></mt-field>
		<div class="field-action">
            <mt-button @click.native="next()" type="primary">下一步</mt-button>
		</div>
	</div>
</template>

<script>

import { Toast } from 'mint-ui-rem-dpr';
import service from '@/services/credit/more-service';

export default{
	data() {
		return {
			detail: {
				card: '',
				cvn2: '',
				date: '',
				idCard: ''
			}
		};
	},
	created() {
		let activeCard = this.$ls.getItem('activeCard');
		if(activeCard && activeCard.card){
			this.detail = activeCard;
		}
	},
	methods: {
        next() {
        	if(this.$validate.isEmpty(this.detail.card)) {
                this.$msg('信用卡卡号不能为空');
                return;
            }
        	this.checkCredit();
        },
        checkCredit() {
			service.checkCredit()
			.then((data) => {
				this.$router.push({name: 'OneActiveDetail'});
			}, (err) => {
	            this.$msg('卡号错误，请核对后重新输入');
			});
		}
	},
	beforeRouteLeave (to, from, next) {
        if(to.meta['groupName'] === from.meta['groupName']){
        	console.log(this.detail);
            this.$ls.setItem('activeCard', this.detail);
        }else{
            this.$ls.removeItem('activeCard');
        }
        next( true );
    }
};

</script>

<style lang="scss">
.more-service-one-active{
}
</style>


