<template>
	<div class="more-service-apply-process-detail">
		<div class="products">
			<div class="product-item">
				<mt-cell title="信用卡卡号：" :value="detail.card"></mt-cell>
				<mt-cell title="申请进度：" :value="detail.process"></mt-cell>
			</div>
		</div>
		<div class="field-action">
			<mt-button @click.native="back" type="primary">返回信用卡首页</mt-button>
		</div>
	</div>
</template>

<script>

export default{
	data() {
		return {
			detail: {
				card: '',
				process: ''
			}
		};
	},
	methods: {
		back() {
			this.$router.push({name: 'Credit'});
		}
	},
	created() {
		this.detail = this.$route.params.detail;
	}
};

</script>

<style lang="scss">
	
.more-service-apply-process-detail{
	.product-title{
		border-bottom: 1px solid #d9d9d9;
	}
	.product-item{
		display: block;
		border-bottom: 1px solid #d9d9d9;
	}
	.mint-field-other{
		top:50%;
	}
	.mint-cell-title{
		width: 4rem;
	}
	.mint-cell:after{
		height: 0;
	}
}

</style>


