<template>
	<div class="pwd-manage-serach-reset">
        <div class="cell-part">
    		<div class="head-hint">请设置新查询密码</div>
    		<cell-password :options="cellPassword1"></cell-password>
    		<cell-password :options="cellPassword2"></cell-password>
        </div>
        <div class="tip-hint">
            两次输入的密码不一致
        </div>
		<div class="button-bottom">
			<mt-button @click.native="submit" type="primary" size="large">确认重置</mt-button>
			<dialog-sms-verify ref="dialogSmsVerify" :options=" dialogSmsVerify "></dialog-sms-verify>
		</div>
	</div>
</template>

<script>

import service from '@/services/credit/manage';

export default {
	data() {
		return {
			cellPassword1: {
                title: '新查询密码',
                rule: 'number' // number or mix
            },
            cellPassword2: {
                title: '确认新查询密码',
                rule: 'number' // number or mix
            },
            dialogSmsVerify: {
                title: '输入验证码完成重置',
                btnConfirmText: '完成重置',
                btnGetText: '获取验证码'
            }
		};
	},
	methods: {
		submit() {
            this.$refs.dialogSmsVerify.show().then((data) => {
                this.resetSearchPwd();
            }, (data) => {
            });
        },
        resetSearchPwd() {
        	service.resetSearchPwd()
        	.then((data) => {
        		this.$router.push({name: 'CreditManagePwdSearchSuccess', params: {type: 'reset'}});
        	}, (err) => {
        		console.log(err);
        	});
        }
	}
};

</script>


