<template>
	<div class="pwd-manage-serach-reset">
        <div class="field-part">
            <mt-cell @click.native="showSelectorPaymentAccount()" title="信用卡卡号：" placeholder="" v-model="card" is-link></mt-cell>
        </div>
        <div class="field-part">
            <mt-field v-model="card1" label="CVN2" placeholder="卡片背面签名栏后3位数字"></mt-field>
        </div>
        <div class="tip-hint">
            账户信息有误，请重新输入
        </div>
        <div class="field-part">
            <div class="head-hint">请设置新交易密码</div>
            <cell-password :options="cellPassword1"></cell-password>
            <cell-password :options="cellPassword2"></cell-password>
        </div>
        <div class="tip-hint">
            两次输入的密码不一致
        </div>
        <div class="button-bottom">
            <mt-button @click.native="submit" type="primary" size="large">确认重置</mt-button>
            <dialog-sms-verify ref="dialogSmsVerify" :options=" dialogSmsVerify "></dialog-sms-verify>
        </div>
        <selector-payment-account ref="selectorPaymentAccount" :options="selectorPaymentAccount" ></selector-payment-account>
    </div>
</template>

<script>

import service from '@/services/credit/manage';

export default {
	data() {
		return {
			card: '',
			card1: '',
			cellPassword1: {
                title: '新交易密码',
                rule: 'number' // number or mix
            },
            cellPassword2: {
                title: '确认新交易密码',
                rule: 'number' // number or mix
            },
            dialogSmsVerify: {
                title: '输入验证码完成重置',
                btnConfirmText: '完成重置',
                btnGetText: '获取验证码'
            },
            selectorPaymentAccount: {
				type: 'credit',
                checked: {},
                showAddBtn: false,
                showWarn: false,
                accountList: [
                    {
                        id: 1,
                        name: '郭振宇',
                        account: '625228***0089',
                        type: '储蓄卡',
                        isDefault: true,
                        idCardType: '身份证',
                        idCard: '550505****50505',
                        status: false
                    },
                    {
                        id: 2,
                        name: '郭振宇2',
                        account: '625228***0025',
                        type: '储蓄卡',
                        status: true
                    },
                    {
                        id: 3,
                        name: '郭振宇3',
                        alias: '',
                        account: '625228***0109',
                        type: '储蓄卡',
                        bankName: '安徽农金'
                    }
                ]
            }
		};
	},
	methods: {
		showSelectorPaymentAccount() {
            this.$refs.selectorPaymentAccount.show().then(data => {
                this.selectorPaymentAccount.checked = data.item;
                this.card = data.item.account;
            }, data => {
            });
        },
		submit() {
            this.$refs.dialogSmsVerify.show().then((data) => {
                this.resetTransactionhPwd();
            }, (data) => {
            });
        },
        resetTransactionhPwd() {
            service.resetTransactionhPwd()
            .then((data) => {
                this.$router.push({name: 'CreditManagePwdTransactionSuccess', params: {type: 'reset'}});
            }, (err) => {
                console.log(err);
            });
        }
	},
	created() {
		this.card = this.$route.params.card;
	}
};

</script>


