<template>
	<div class="credit-manage-set-service">
		<div class="cell-part">
			<mt-cell title="账单寄送方式" :value="detail.sendType"></mt-cell>
			<mt-cell title="电子邮箱" :value="detail.mail"></mt-cell>
		</div>
		<div class="cell-part">
			<mt-cell title="邮寄地址" :value="detail.addressType"></mt-cell>
			<mt-cell title="家庭地址" :value="detail.address1"></mt-cell>
			<mt-cell title="单位地址" value="中国安徽省合肥市寿春路保理大厦C栋2308室" class="address">
			</mt-cell>
		</div>
		<mt-button @click.native="update" type="primary">修改</mt-button>
	</div>
</template>

<script>

import service from '@/services/credit/manage';

export default {
	data() {
		return {
			detail: {
				sendType: '',
				mail: '',
				addressType: '',
				address1: '',
				address2: ''
			}
		};
	},
	methods: {
		update() {
			this.$router.push({name: 'CreditManageSetServiceUpdate'});
		},
		getSetServiceDetail() {
			service.getSetServiceDetail()
			.then((data) => {
			}, (err) => {
				console.log(err);
			});
		}
	},
	created() {
		this.getSetServiceDetail();
	}
};

</script>

<style lang="scss">
	
.credit-manage-set-service{
	.address{
		.mint-cell-value{
			max-width: 75%;
			text-align: right;
			line-height: 36px;
		}
	}
}

</style>


