<template>
	<div class="more-service-patch-card">
        <mt-cell @click.native="showSelectorPaymentAccount()" title="信用卡卡号：" placeholder="" v-model="detail.account" is-link>
        </mt-cell>
		<selector-payment-account ref="selectorPaymentAccount" :options="selectorPaymentAccount" ></selector-payment-account>
        <div v-show="detail.status && detail.account">
            <mt-cell class="mint-select" title="补卡方式：" is-link>
                <select class="mint-field-core" v-model="detail.type">
                    <option value="">不限</option>
                    <option value="1">即将发行</option>
                    <option value="2">可购买</option>
                    <option value="3">已发行</option>
                </select>
            </mt-cell>
            <mt-cell class="mint-select" title="邮寄地址：" is-link>
                <select class="mint-field-core" v-model="detail.address">
                    <option value="">家庭地址</option>
                    <option value="1">单位地址</option>
                </select>
            </mt-cell>
            <mt-cell>
                <a @click="toUpdateAddress" href="javascript:;" class="forget-pwd">如果地址有变更，请修改</a>
            </mt-cell>
    		<cell-password :options=" cellPassword "></cell-password>
    		<mt-cell class="mark" title="申请卡片挂失可能会产生手续费">
    			<a @click="toResetPwd" href="javascript:;" class="forget-pwd">忘记查询密码？</a>
    		</mt-cell>
    		<div class="field-action">
                <submit-button @submit="submit" :submitStatus="submitStatus" value="确认补卡"></submit-button>
    		</div>
        </div>
        <div v-show="!detail.status && detail.account" class="field-warn color-red">
            卡片未挂失，不能补卡
        </div>
	</div>
</template>

<script>

import service from '@/services/credit/more-service';

export default{
	data() {
		return {
			detail: {
                account: '',
                type: '',
                address: ''
            },
            submitStatus: false,
            cellPassword: {
                title: '查询密码：',
                rule: 'number' // number or mix
            },
			selectorPaymentAccount: {
				type: 'credit',
                checked: {},
                showWarn: false,
                accountList: [
                    {
                        id: 1,
                        name: '郭振宇',
                        account: '625228***0089',
                        type: '储蓄卡',
                        isDefault: true,
                        idCardType: '身份证',
                        idCard: '550505****50505',
                        status: false
                    },
                    {
                        id: 2,
                        name: '郭振宇2',
                        account: '625228***0025',
                        type: '储蓄卡',
                        status: true
                    },
                    {
                        id: 3,
                        name: '郭振宇3',
                        alias: '',
                        account: '625228***0109',
                        type: '储蓄卡',
                        bankName: '安徽农金'
                    }
                ]
            }
		};
	},
    watch: {
        'detail.account': function(val) {
            if(val){
                this.submitStatus = true;
            }else{
                this.submitStatus = false;
            }
        }
    },
	methods: {
		showSelectorPaymentAccount() {
            this.$refs.selectorPaymentAccount.show().then(data => {
                this.selectorPaymentAccount.checked = data.item;
                this.detail.account = data.item.account;
                this.detail.status = data.item.status;
            }, data => {
            });
        },
        submit() {
        	this.submitPatchCard();
        },
        toUpdateAddress() {
            this.$router.push({name: 'CreditManageSetService'});
        },
        toResetPwd() {
            this.$router.push({name: 'CreditManagePwdSearchReset'});
        },
        initAccountSelected() {
            if(this.detail.account){
                let accountList = this.selectorPaymentAccount.accountList;
                for(let i = 0; i < accountList.length; i++){
                    if(accountList[i].account == this.detail.account){
                        this.selectorPaymentAccount.checked = accountList[i];
                        this.detail.status = accountList[i].status;
                    }
                }
            }
        },
        submitPatchCard() {
            service.submitPatchCard()
            .then((data) => {
                this.$router.push({name: 'PatchCardSuccess'});
            }, (err) => {
                this.$msg('该卡片已补卡');
            });
        }
	},
    mounted() {
        this.initAccountSelected();
    },
	created() {
        this.detail.account = this.$route.params.card;
	},
	beforeRouteLeave (to, from, next) {
        if(to.meta['groupName'] === from.meta['groupName']){
            this.$store.dispatch('updateKeepSearch', {key: 'account', value: this.account});
        }else{
            this.$store.dispatch('updateKeepSearch', {key: 'account', value: ''});
        }
        next( true );
    }
};

</script>

<style lang="scss">
.more-service-patch-card{
	.mint-cell-title,{
		width: 3.6rem;
	}
    .mint-cell.mint-select{
        .mint-cell-title{
            width: 3.6rem;
        }
    }
    .mark .mint-cell-text{
    	font-size: 12px;
    }
	.forget-pwd{
		color: #26a2ff;
		font-size: 12px;
	}
    .color-red{
        color: red;
    }
}
</style>


