<template>
	<div class="pwd-manage-transaction-update">
        <div class="field-part">
            <mt-cell @click.native="showSelectorPaymentAccount()" title="信用卡卡号：" placeholder="" v-model="card" is-link></mt-cell>
        </div>
        <div class="field-part">
            <div class="head-hint">请输入原交易密码</div>
            <cell-password :options="cellPassword1"></cell-password>
        </div>
        <div class="tip-hint">
            原密码错误，请重新输入
        </div>
        <div class="field-part">
            <div class="head-hint">请设置新交易密码</div>
            <cell-password :options="cellPassword2"></cell-password>
            <cell-password :options="cellPassword3"></cell-password>
        </div>
        <div class="tip-hint">
            两次输入的密码不一致
        </div>
        <div class="button-bottom">
            <mt-button @click.native="submit" type="primary" size="large">确认修改</mt-button>
        </div>
        <selector-payment-account ref="selectorPaymentAccount" :options="selectorPaymentAccount" ></selector-payment-account>
	</div>
</template>

<script>

import service from '@/services/credit/manage';

export default {
	data() {
		return {
			card: '',
			cellPassword1: {
                title: '原交易密码',
                rule: 'number' // number or mix
            },
            cellPassword2: {
                title: '新交易密码',
                rule: 'number' // number or mix
            },
            cellPassword3: {
                title: '确认新交易密码',
                rule: 'number' // number or mix
            },
            selectorPaymentAccount: {
				type: 'credit',
                checked: {},
                showAddBtn: false,
                showWarn: false,
                accountList: [
                    {
                        id: 1,
                        name: '郭振宇',
                        account: '625228***0089',
                        type: '储蓄卡',
                        isDefault: true,
                        idCardType: '身份证',
                        idCard: '550505****50505',
                        status: false
                    },
                    {
                        id: 2,
                        name: '郭振宇2',
                        account: '625228***0025',
                        type: '储蓄卡',
                        status: true
                    },
                    {
                        id: 3,
                        name: '郭振宇3',
                        alias: '',
                        account: '625228***0109',
                        type: '储蓄卡',
                        bankName: '安徽农金'
                    }
                ]
            }
		};
	},
	methods: {
		showSelectorPaymentAccount() {
            this.$refs.selectorPaymentAccount.show().then(data => {
                this.selectorPaymentAccount.checked = data.item;
                this.card = data.item.account;
            }, data => {
            });
        },
		submit() {
            this.updateTransactionPwd();
        },
        updateTransactionPwd() {
            service.updateTransactionPwd()
            .then((data) => {
                this.$router.push({name: 'CreditManagePwdTransactionSuccess', params: {type: 'update'}});
            }, (err) => {
                console.log(err);
            });
        }
	}
};

</script>


