<template>
	<div class="pwd-manage-serach-apply">
        <div class="cell-part">
    		<div class="head-hint">请设置查询密码</div>
            <cell-password :options="cellPassword1"></cell-password>
            <cell-password :options="cellPassword2"></cell-password>
        </div>
        <div class="tip-hint">
            两次输入的密码不一致
        </div>
		<div class="button-bottom">
			<mt-button @click.native="submit" type="primary" size="large">确认申请</mt-button>
			<dialog-sms-verify ref="dialogSmsVerify" :options=" dialogSmsVerify "></dialog-sms-verify>
		</div>
	</div>
</template>

<script>

import service from '@/services/credit/manage';

export default {
	data() {
		return {
			cellPassword1: {
                title: '查询密码',
                rule: 'number' // number or mix
            },
            cellPassword2: {
                title: '确认查询密码',
                rule: 'number' // number or mix
            },
            dialogSmsVerify: {
                title: '输入验证码完成申请',
                btnConfirmText: '完成申请',
                btnGetText: '获取验证码'
            }
		};
	},
	methods: {
		submit() {
            this.$refs.dialogSmsVerify.show().then((data) => {
                this.applySearchPwd();
            }, (data) => {
            });
        },
        applySearchPwd() {
        	service.applySearchPwd()
        	.then((data) => {
        		this.$router.push({name: 'CreditManagePwdSearchSuccess', params: {type: 'apply'}});
        	}, (err) => {
        		console.log(err);
        	});
        }
	}
};

</script>


