<template>
	<div class="credit-manage">
		<div class="set-type">
			<a @click="toPage('CreditManageAutoRepay')" href="javascript:;">
				<span class="auto">自动还款</span>
			</a>
			<a @click="toPage('CreditManageSetService')" href="javascript:;">
				<span class="service">服务设置</span>
			</a>
			<a @click="toPage('CreditManagePwdManage')" href="javascript:;">
				<span class="pwd">密码管理</span>
			</a>
		</div>
		<div class="credit-list" v-if="creditList.length > 0">
			<bank-card :bank-card-list="creditList" @bank-card-click="bankCardClick" cardType="credit"></bank-card>
		</div>
		<div class="none-credit" v-else>
			<div class="bg">
                <div class="bg-pic"></div>
                <p class="bg-msg">您暂未添加信用卡</p>
            </div>
		</div>
		<div class="fixed-footer">
			<mt-button @click.native="addCredit" size="large" type="primary">
				<img src="../../../assets/img/bank/card/add-white@2x.png" class="slot-icon" slot="icon" width="18" height="18">添加信用卡
			</mt-button>
		</div>
	</div>
</template>

<script>

import service from '@/services/my/my-bank-card';

export default {
	data() {
		return {
			creditList: []
		};
	},
	methods: {
		toPage(name) {
			this.$router.push({name: name});
		},
		bankCardClick(item) {
			this.$router.push({name: 'CreditManageCardSet', params: {card: item.account}});
		},
		getBankCreditList() {
			service.getBankCreditList().then(data => {
				this.creditList = data.data;
			});
		},
		addCredit() {
			this.$router.push({name: 'MyBankCardCreditAdd'});
		}
	},
	created() {
		this.getBankCreditList();
	}
};

</script>

<style lang="scss">

.credit-manage{
	.set-type{
		display: flex;
		justify-content: space-between;
		background-color: #fff;
		box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
		padding: 40px 44px;
		a{
			display: inline-block;
			font-size: 15px;
			color: #666666;
			text-align: center;
			span {
				position: relative;
				padding-left: 42px;
			}
			span:after {
				content: '';
				display: block;
				position: absolute;
				top: 0;
				left: 0;
				width: 32px;
				height: 32px;
			}
			.auto:after {
				@include image-set('../../../assets/img/credit/auto');
			}
			.service:after {
				top: 8px;
				@include image-set('../../../assets/img/credit/service');
			}
			.pwd:after {
				@include image-set('../../../assets/img/credit/pwd');
			}
		}
	}
	.credit-list{
		padding: 30px;
	}
	.none-credit{
		padding-top: 144px;
        .bg {
            text-align: center;
            .bg-pic {
                margin: auto;
                width: 168px;
                height: 114px;
                @include image-set('../../../assets/img/credit/none-credit');
            }
            .bg-msg {
                margin-top: 40px;
                font-size: 17px;
                color: #666666;
            }
        }
	}
}

</style>


