<template>
	<div class="more-service-quick-loss-success">
		<div class="submit-contain">
			<div class="result-icon">
                <i class="mintui mintui-success"></i>
            </div>
			<div>挂失成功</div>
			<div>
				如需解挂请持本人身份证件到安徽农金任一营业网点办理；如需补卡请使用闪电补卡功能
			</div>
			<mt-cell title="信用卡卡号：" value="625228***0025"></mt-cell>
			<mt-cell title="卡片当前状态：" value="3636363663636****225"></mt-cell>
			<mt-cell title="卡片挂失费用：" value="3636363663636****225"></mt-cell>
			<div class="field-action">
				<mt-button @click.native="toPatchCard" type="primary">闪电补卡</mt-button>
			</div>
			<div class="field-action">
				<mt-button @click.native="back" type="primary">返回信用卡首页</mt-button>
			</div>
		</div>
	</div>
</template>

<script>
	
export default {
	data() {
		return {};
	},
	methods: {
		toPatchCard() {
			this.$router.push({name: 'PatchCard', params: {card: '625228***0025'}});
		},
		back() {
			this.$router.push({name: 'Credit'});
		}
	}
};

</script>

<style lang="scss">
.more-service-quick-loss-success{
	position: absolute;
	width: 100%;
	height:100%;
	top: 0;
	left: 0;
	background-color: #fff;
	z-index: 1;
	
	font-size: 14px;
	.submit-contain{
		margin-top: 50%;
	}
	.submit-contain > div{
		text-align: center;
	}

}
</style>


