<template>
	<div class="pwd-manage-serach-update">
        <div class="field-part">
    		<div class="head-hint">请输入原查询密码</div>
    		<cell-password :options="cellPassword1"></cell-password>
        </div>
        <div class="tip-hint">
            原密码错误，请重新输入
        </div>
        <div class="field-part">
    		<div class="head-hint">请设置新查询密码</div>
    		<cell-password :options="cellPassword2"></cell-password>
    		<cell-password :options="cellPassword3"></cell-password>
        </div>
        <div class="tip-hint">
            过于简单，请重新输入您的密码
        </div>
		<div class="button-bottom">
			<mt-button @click.native="submit" type="primary" size="large">确认修改</mt-button>
		</div>
	</div>
</template>

<script>

import service from '@/services/credit/manage';

export default {
	data() {
		return {
			cellPassword1: {
                title: '原查询密码',
                rule: 'number' // number or mix
            },
            cellPassword2: {
                title: '新查询密码',
                rule: 'number' // number or mix
            },
            cellPassword3: {
                title: '确认新查询密码',
                rule: 'number' // number or mix
            }
		};
	},
	methods: {
		submit() {
            this.updateSearchPwd();
        },
        updateSearchPwd() {
        	service.updateSearchPwd()
        	.then((data) => {
        		this.$router.push({name: 'CreditManagePwdSearchSuccess', params: {type: 'update'}});
        	}, (err) => {
        		console.log(err);
        	});
        }
	}
};

</script>


